@import '../../styles/variables.scss';

.chat-header {
    width: 100%;
    padding: 6px 35px;
    background-color: $bg-elements;
    border-bottom: $border;
}

.chat-header__text {
    font-size: 23px;
    color: $color-text;
    padding: 0px 5px;
    font-weight: 600;
    font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
}
.chat-header__wrap{
    display: flex;
    align-items: center;
}
.chat-header__img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin: 5px;
}